                url('https://cdn.discordapp.com/attachments/1397420970767024168/1402600492520968333/1754476319-picsay.jpg?ex=68948100&is=68932f80&hm=acb6d57d135e925a0dddf4b1bb3d8a24e9a39a4502954d1347b9a669587b666a&');
