.movies-section {
    padding: 2rem 1rem;
    background-color: #000000;
    min-height: 100vh;
    text-align: center;
}

.movies-title {
    font-family: 'Cinzel', serif;
    font-size: 1.5rem;
    color: #ffffff;
    margin-bottom: 2rem;
