    letter-spacing: 3px;
    font-weight: 600;
    text-shadow: 
        2px 2px 4px rgba(0, 0, 0, 0.6),
        0 0 20px rgba(255, 255, 255, 0.15);
}

.movies-grid {
    display: grid;
    grid-template-columns: repeat(5, 170px); /* Fixed width columns */
    grid-gap: 1rem;
    gap: 1rem; /* Increased gap between cards */
