.search-page {
  background-color: #0d0d0d;
  min-height: 100vh;
  padding-top: 80px;
}

.search-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 2rem 1.5rem;
}

.search-title {
  font-family: 'Cinzel', serif;
  font-size: 1.8rem;
  color: #fff;
  margin: 0 0 2rem;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 600;
  text-shadow:
    2px 2px 4px rgba(0,0,0,0.8),
    0 0 25px rgba(255,255,255,0.2);
}

.search-loading,
.search-no-results {
  text-align: center;
  color: #ccc;
  font-size: 1.1rem;
  margin: 3rem 0;
  font-family: 'Montserrat', sans-serif;
}

.search-loading {
  color: #fff;
  font-size: 1.2rem;
}

.search-no-results {
  color: #888;
}

/* Results count */
.search-results-count {
  text-align: center;
  color: #aaa;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
  font-family: 'Montserrat', sans-serif;
}

/* Horizontal scrolling container */
.search-results-wrapper {
  position: relative;
  margin: 0 -1.5rem; /* Extend to full width */
}

.movies-grid-horizontal {
  display: flex;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 1rem 1.5rem 2rem;
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: rgba(255,255,255,0.3) transparent;
}

.movies-grid-horizontal::-webkit-scrollbar {
  height: 8px;
}

.movies-grid-horizontal::-webkit-scrollbar-track {
  background: rgba(255,255,255,0.1);
  border-radius: 4px;
}

.movies-grid-horizontal::-webkit-scrollbar-thumb {
  background: rgba(255,255,255,0.3);
  border-radius: 4px;
}

.movies-grid-horizontal::-webkit-scrollbar-thumb:hover {
  background: rgba(255,255,255,0.5);
}

/* Movie cards in horizontal layout */
.search-page .movie-card {
  flex: 0 0 200px; /* Fixed width, no shrink/grow */
  width: 200px;
  background: linear-gradient(145deg, #1a1a1a, #0f0f0f);
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: 
    0 4px 15px rgba(0,0,0,0.3),
    0 2px 5px rgba(0,0,0,0.2);
  border: 1px solid rgba(255,255,255,0.05);
}

.search-page .movie-card:hover {
  transform: translateY(-8px) scale(1.05);
  box-shadow: 
    0 12px 30px rgba(0,0,0,0.4),
    0 6px 15px rgba(0,0,0,0.3);
  border-color: rgba(255,255,255,0.15);
  z-index: 10;
}

.search-page .movie-poster {
  position: relative;
  padding-top: 140%; /* 2:2.8 aspect ratio */
  overflow: hidden;
}

.search-page .movie-poster img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.search-page .movie-card:hover .movie-poster img {
  transform: scale(1.08);
}

.search-page .movie-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.95) 0%,
    rgba(0, 0, 0, 0.8) 40%,
    rgba(0, 0, 0, 0.6) 70%,
    rgba(0, 0, 0, 0) 100%
  );
}

.search-page .movie-title {
  color: #ffffff;
  font-size: 0.95rem;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.8);
  text-align: center;
  letter-spacing: 0.5px;
  line-height: 1.2;
}

.search-page .movie-info {
  padding: 1rem;
  background: linear-gradient(180deg, #1a1a1a 0%, #111 100%);
}

.search-page .movie-meta {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 6px;
  gap: 6px;
  color: #bbb;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
  font-family: 'Montserrat', sans-serif;
}

.search-page .movie-genres {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 3px;
  gap: 3px;
  justify-content: center;
}

.search-page .genre-tag {
  background: linear-gradient(135deg, rgba(255,255,255,0.15), rgba(255,255,255,0.08));
  color: #fff;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 0.65rem;
  font-weight: 500;
  border: 1px solid rgba(255,255,255,0.1);
  text-transform: uppercase;
  letter-spacing: 0.3px;
}

/* Scroll hints */
.scroll-hint {
  text-align: center;
  color: #666;
  font-size: 0.85rem;
  margin-top: 1rem;
  font-family: 'Montserrat', sans-serif;
  animation: fadeInOut 3s ease-in-out infinite;
}

@keyframes fadeInOut {
  0%, 100% { opacity: 0.4; }
  50% { opacity: 0.8; }
}

/* Responsive design */
@media (max-width: 768px) {
  .search-container {
    padding: 1.5rem 1rem;
  }
  
  .search-title {
    font-size: 1.4rem;
    margin-bottom: 1.5rem;
  }
  
  .search-results-wrapper {
    margin: 0 -1rem;
  }
  
  .movies-grid-horizontal {
    padding: 1rem;
    grid-gap: 1rem;
    gap: 1rem;
  }
  
  .search-page .movie-card {
    flex: 0 0 160px;
    width: 160px;
  }
  
  .search-page .movie-title {
    font-size: 0.85rem;
  }
}

@media (max-width: 480px) {
  .search-title {
    font-size: 1.2rem;
    letter-spacing: 2px;
  }
  
  .movies-grid-horizontal {
    grid-gap: 0.8rem;
    gap: 0.8rem;
  }
  
  .search-page .movie-card {
    flex: 0 0 140px;
    width: 140px;
  }
  
  .search-page .movie-title {
    font-size: 0.8rem;
  }
}

/* Global styles for the website */

body {
    margin: 0;
    padding: 0;
    background-color: #0d0d0d;  /* Dark gray background */
    color: #ffffff;
}

.app {
    min-height: 100vh;
    background-color: #1a1a1a;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: inherit;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}
